// @import 'vars'; если используете scss-переменные - подключите сюда vars, но удалите root из _vars.scss

@import "./components/header";

$blue:#069CCE;

*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
body{
   // background: #151515;
   background: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5) ), url("../img/second-background.svg");
   background-position: center;
   background-repeat: no-repeat;
   background-size:cover ;
  
}
.header{
    background: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url('../img/header_background_image.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    .nav{
        padding-top: 20px;
        display: flex;
        .logo{
            margin: 0px;
            padding: 0px;
            margin-left: 130px;
            width: 200px;
        }
        img{
            display: block;
            margin-left: auto;
            margin-right: auto;
            width: 50%;
            cursor: pointer;
            align-self: center;
        }
        .header-social{
            display: flex;
            margin-left: auto;
            margin-right: 150px;
           
        }
        a{
            align-self: center;
        img{
            margin: 0 10px;
            cursor: pointer;
           
        }}
        ul{
            display: flex;
            align-self: center;
            text-align: center;
            margin-left: auto;
            margin-right: auto;
            cursor: pointer;
            transition: 0.3s;
            li{
                margin: 0 20px;
                color: white;
            }
            li:hover{
                color: $blue;
                transition: 0.3s;
            }
        }
    }

   .body{
       display: flex;
       //width: 80%;
       height: auto;
       padding-bottom: 50px;
       .body-text-column{
           text-align: left;
            margin-left: 150px;
            width: 100%;
            max-width:600px;
           h1{
               color: #fff;
               font-size: 50px;
           }
           h3{
               color: #fff;
               font-size: 17.5px;
               font-weight: 500;
           }
           .body-buttons{
            margin-top: 20px;
            .btn-glow-in{
                background:none;
                border:none;
                color: white;
                background: $blue;
                padding: 0.8em 2em;
                box-shadow: 0px 0px 20px 0px $blue;
                border-radius: 5px;
                cursor: pointer;
            }
            .btn-glow-out{
                background: none;
                border:none;
                color: $blue;
                 margin: 0 20px;
                 border: 2px $blue solid;
                 padding: 0.8em 2em;
                 border-radius: 5px;
                 cursor: pointer;
                 transition: 0.3s;
            }
            .btn-glow-out:hover{
             background: $blue;
             color: white;
             box-shadow: 0px 0px 20px 0px $blue;
             transition: 0.3s;
            }
        }
    }
  
       }
       
}
.material-icons.md-light { color: rgba(255, 255, 255, 1); 
}
