@media (max-width:1140px){
        .logo{
            width: 300px !important;
            max-width: none !important;
            min-width: 300px;
            margin-left: 5px !important;
        }
        
}

$mobile_width: 650px;
.mobile-nav{
    visibility: hidden;
}
@media (min-width:$mobile_width){
    .form{
        display: block;
        margin: auto;
        form{
            width: 10%;
        }
    }
    .mobile-nav{
        display: none;
    }
    .menu{
        visibility:  hidden;
        display: none;
    }
}

@media (max-width:$mobile_width){
    .nav-panel{
        visibility: hidden !important;
    }
    .second-nav-panel{
        visibility: hidden;
    }

    .logo{
        margin-left: 10px !important;
    }
    .second-social{
        margin-left: 10px !important;
    }
    .body-text-column{
        margin-left: 30px !important;
        h1{font-size: 30px !important; }
    }
    .body-buttons{
        display: block !important;
        button{
            display: block !important;
            margin: 30px 0px !important;
            width: 250px;
            height: 50px;
        }
    }

    .nav{
        width: 100%;
        text-align: center;
        visibility: hidden;
        padding-top: 0 !important;
        margin-top: 0 !important;
    }
    .mobile-nav{
        display: flex;
        visibility: visible;
        width: 100%;
        
        img{
            width: 100px;
            margin-left: 30px;
        }
        span{
            margin-left: auto;
            margin-right: 30px;
            align-self: center;
            font-size: 30px;
        }
    }
    .conditions{
        
        a{
            font-size: 20px !important;
        }
        ul{
            padding: 0 !important;
        }
    }
}

.preloader {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #e0e0e0;  
    z-index: 1001;
  }
  
  .preloader__row {
    position: relative;
    top: 50%;
    left: 50%;
    width: 70px;
    height: 70px;
    margin-top: -35px;
    margin-left: -35px;
    text-align: center;
    animation: preloader-rotate 2s infinite linear;
  }
  
  .preloader__item {
    position: absolute;
    display: inline-block;
    top: 0;
    background-color: $blue;
    border-radius: 100%;
    width: 35px;
    height: 35px;
    animation: preloader-bounce 2s infinite ease-in-out;
  }
  
  .preloader__item:last-child {
    top: auto;
    bottom: 0;
    animation-delay: -1s;
  }
  
  @keyframes preloader-rotate {
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes preloader-bounce {
  
    0%,
    100% {
      transform: scale(0);
    }
  
    50% {
      transform: scale(1);
    }
  }
  
  .loaded_hiding .preloader {
    transition: 0.3s opacity;
    opacity: 0;
  }
  
  .loaded .preloader {
    display: none;
  }
.menu{
    background-color: #ffffffe3;
    position:fixed;
    right: 0;
    height: 100vh;
    top: 0;
    padding: 1em;
    ul{
        li{
            cursor: pointer;
            font-size:20px;
            margin-top: 20px;
            background: none;
            border: 2px $blue solid;
            padding: 0.5em;
            border-radius: 5px;
            color: black;
        }
    }
    .menu-active{
        text-align: center;
        text-transform: uppercase;
        box-shadow: 0px 0px 10px 0px $blue;
        background: $blue;
        color: white;
    }
    span{
        display: block !important;
        margin-top: 30px !important;
        text-align: center;
        font-size: 30px !important;
        background: $blue;
        padding: 0.5em;
        border-radius: 5px;
        color: white;
        box-shadow: 0px 0px 10px 0px $blue;
    }
}

.form{
    width: 100%;
    height: 100vh;
    position: fixed;
    background: #000000cb;
    .form_upper{
        display: flex;
        justify-content: center;
        align-items: center;
        align-self: center;
        width: 100%;
        height: 100vh;
        .form-upper-child{
            display: block;
            margin: 0 auto;
            color: white;
            font-size: 10px;
        }
    }
    
    form{
        display: block;
        margin: 0 auto;
        input[type="text"]{
            font-size: 20px;
            width: 100%;
            background: none;
            border: none;
            border-bottom: 2px $blue solid;
            outline: none;
            margin-right: 20px;
            padding: 1em;
            align-self: center;
            color: white;
        }
        input[type="submit"]{
            font-size: 15px;
            text-align: center;
            display: block;
            margin: 0 auto;
                background: none;
                border:none;
                color: $blue;
                 margin: 0 20px;
                 border: 2px $blue solid;
                 padding: 0.8em 2em;
                 border-radius: 5px;
                 cursor: pointer;
                 transition: 0.3s;
                 margin-top: 20px;
        }
        input[type="submit"]:hover{
             background: $blue;
             color: white;
             box-shadow: 0px 0px 20px 0px $blue;
             transition: 0.3s;
        }
    }
    
}

.conditions{
    color: white;
    width: 80%;
    margin: auto;
    margin-top: 40px;
    
    a{
        font-size: 30px;
    }
    ul{
        padding-left: 30px;
        li{
            list-style-type:none ;
            margin-top: 10px;
            margin-bottom: 10px;
           margin-left: 10px;
        }
        margin: 20px 0px;
    }
    p{
        font-size: 10px;
    }
    .how-to{
        display: flex;
        align-items: center;
    }
}
